import React from 'react';
import './company-page-list.css';
import { Card, CardContent, List, ListItem, Typography } from '@material-ui/core';
import { Link } from 'react-router-dom';
import { organizationsList } from '../../../mock/company';

const CompanyPageList = () => {
  return (
    <div className="company-list-page">
      <div>
        <Card style={{ width: '200px', textAlign: 'center' }}>
          <CardContent>
            <Typography>Список компаний</Typography>
          </CardContent>
        </Card>
      </div>

      {organizationsList.map(({ id, inn, name, contract_status, region, dealer }) => (
        <Link key={id} to={`/company/${id}`} className="company-list-link">
          <Card className="company-list-item">
            <List>
              <ListItem>ИНН: {inn}</ListItem>
              <ListItem>Имя организации: {name}</ListItem>
              <ListItem>Статус контракта: {contract_status}</ListItem>
              <ListItem>Регион: {region}</ListItem>
              <ListItem>Регион: {dealer ? dealer.name : 'нет'}</ListItem>
            </List>
          </Card>
        </Link>
      ))}
    </div>
  );
};

export default CompanyPageList;
